<template>
  <div>
    <v-sidebar :treeData=treeData v-on:childByValue="childByValue"></v-sidebar>
    <div class="content-box" :class="{'content-collapse':collapse,minPaddingLeft:minPaddingLeft,maxPaddingLeft:maxPaddingLeft}">
      <div class="content contentselfdefine">
        <div class="wrap">
          <transition name="move" mode="out-in">
            <keep-alive :include="tagsList">
              <router-view></router-view>
            </keep-alive>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import vHead from "../../common/Header.vue";
import vSidebar from "../../common/Sidebar.vue";
import vTags from "../../common/Tags.vue";
import bus from "../../common/bus";
export default {
  data() {
    return {
      tagsList: [],
      treeData: [
        // {
        //   icon: "el-icon-dd",
        //   index: "dashboard",
        //   title: "工程师站运行监视"
        // },
        // {
        //   icon: "el-icon-nyjl",
        //   index: "dashboard7",
        //   title: "需求用户监视"
        // },
        {
          icon: "el-icon-dn",
          index:"#",
          title: "分布式资源管理",
          subs: [
            {
              index: "runWebAnalysis",
              title: "运行分析"
            }

          ]
        },
        {
          icon: "el-icon-dn",
          index:"##",
          title: "分布式光伏可行性分析",
          subs: [
            {
              index: "programWebAnalysis",
              title: "新能源接入分析"
            },
            {
              index: "programWebAssess",
              title: "光伏资源评估"
            },
            {
              index: "programWebProject",
              title: "接入项目管理"
            },
            {
              index: "programWebScheme",
              title: "接入方案生成"
            },
            {
              index: "programWebScheme",
              title: "接入方案技术评估"
            },
            {
              index: "programWebScheme",
              title: "接入方案成本估算"
            },
            {
              index: "programWebScheme",
              title: "接入方案经济效益评估"
            }

          ]
        },
        {
          icon: "el-icon-dn",
          index:"###",
          title: "虚拟电厂仿真",
          subs: [
            {
              index: "virtualWebManage",
              title: "虚拟电厂管理"
            },
             {
              index: "virtualWebDispatch",
              title: "虚拟电厂调度策略"
            }

          ]
        },

        {
          icon: "el-icon-dn",
          index: "####",
          title: "分布式资源运维托管",
          subs: [
            {
              index: "maintenanceWebTrust",
              title: "托管信息管理"
            },
            {
              index: "maintenanceWebDefect",
              title: "缺陷管理"
            },
            {
              index: "maintenanceWebTour",
              title: "巡视管理"
            },
            {
              index: "maintenanceWebInspection",
              title: "检修管理"
            },
            {
              index: "maintenanceWebWork",
              title: "工单管理"
            },

          ]
        }
      ],
      collapse: false,
      minPaddingLeft: false,
      maxPaddingLeft: true
    };
  },

  methods: {
    childByValue: function(childValue) {
      console.log("childValue:"+childValue);
      var windowWidth = window.innerWidth;
      // if (windowWidth >= 1366) {  //笔记本上网页宽度小于1366，组件收缩效果会出问题
        if (childValue) {
          // childValue就是子组件传过来的值
          this.minPaddingLeft = childValue;
          this.maxPaddingLeft = !childValue;
        } else {
          this.minPaddingLeft = childValue;
          this.maxPaddingLeft = !childValue;
        }
      // } else {
      //   if (childValue) {
      //     // childValue就是子组件传过来的值
      //     this.minPaddingLeft = !childValue;
      //   } else {
      //     console.log(childValue);
      //     this.minPaddingLeft = !childValue;
      //   }
      // }
    }
  },
  components: {
    vHead,
    vSidebar,
    vTags
  },
  created() {
    //  bus.$emit("menuitem", 0);
    var windowWidth = window.innerWidth;
    if (windowWidth >= 1366) {
      this.maxPaddingLeft = true;
    }
    bus.$on("collapse", msg => {
      this.collapse = msg;
    });

    // 只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
    bus.$on("tags", msg => {
      let arr = [];
      for (let i = 0, len = msg.length; i < len; i++) {
        msg[i].name && arr.push(msg[i].name);
      }
      this.tagsList = arr;
    });
  },
  mounted() {

  }
};
</script>

<style>
.content.contentselfdefine{padding:15px}
</style>



